export const mainScene = new Scenes.BaseScene<MyContext>('main');

mainScene.enter(async (ctx: MyContext) => {
    const messageText = `[   ]`;

    const mainMenuKeyboard = Markup.inlineKeyboard([
        [
            Markup.button.callback('', 'education'),
        ],
        [
            Markup.button.callback(' ', 'documents'),
            Markup.button.callback('  ', 'clients_management'),
        ],
        [
            Markup.button.callback(' ', 'change_description'),
        ],
        [
            Markup.button.callback(' ', 'change_photo'),
            Markup.button.callback('  ', 'change_schedule'),
        ],
        [
            Markup.button.callback('   ', 'logout')  //   
        ]
    ]);

    if (ctx.callbackQuery?.message) {
        try {
            await ctx.editMessageText(messageText, mainMenuKeyboard);
        }
        catch (error) {
            await ctx.reply(messageText, mainMenuKeyboard);
        }
    } else {
        await ctx.reply(messageText, mainMenuKeyboard);
    }
});

