mainScene.action('logout', async (ctx: MyContext) => {
    try {
        await ctx.answerCbQuery('  ...');
        
        //   
        const confirmKeyboard = Markup.inlineKeyboard([
            [
                Markup.button.callback(' , ', 'confirm_logout'),
                Markup.button.callback(' ', 'cancel_logout')
            ]
        ]);

        await ctx.editMessageText(
            ' ,     ?',
            confirmKeyboard
        );
    } catch (error) {
        console.error('  :', error);
        await ctx.reply(' .  .');
    }
});

//  
mainScene.action('confirm_logout', async (ctx: MyContext) => {
    try {
        await ctx.answerCbQuery();
        
        //    
        const telegramId = ctx.from?.id;
        if (telegramId) {
            try {
                await laravelService.logout(telegramId);
            } catch (error) {
                //  ,    
                console.error('     :', error);
            }
        }

        //        
        await ctx.editMessageText(
            '    .',
            Markup.inlineKeyboard([
                [Markup.button.callback(' ', 'start_login')]
            ])
        );

        //    
        
        return ctx.scene.enter('login_wizard');

    } catch (error) {
        console.error('  :', error);
        await ctx.reply(' .      .');
        
        //         
        return ctx.scene.enter('login_wizard');
    }
});

